import React from "react";
import "./styles.scss";

function ProjectItems({ project }) {
  const {
    title,
    description,
    img,
    stack,
    liveLink,
    github,
    figma,
    demo,
  } = project;
  return (
    <div class="project">
      <figure class="project-preview">
        <a href={liveLink}>
          <img src={img} alt="" />
        </a>
      </figure>
      <figcaption class="project-content">
        <h3>{title}</h3>
        <p>{description}</p>
        <ul class="project-content__skills">
          {stack.map(tool => (
            <li>{tool}</li>
          ))}
        </ul>
        <div class="project-content__buttons">
          {liveLink && (
            <button class="btn btn--transparent btn--dk">
              <a data-scroll href={liveLink} target="_blank">
                View Live
              </a>
            </button>
          )}
          {github && (
            <button class="btn btn--transparent btn--dk">
              <a data-scroll href={github} target="_blank">
                Github
              </a>
            </button>
          )}
          {figma && (
            <button class="btn btn--transparent btn--dk">
              <a data-scroll href={figma} target="_blank">
                Figma
              </a>
            </button>
          )}
          {demo && (
            <button class="btn btn--transparent btn--dk">
              <a data-scroll href={demo} target="_blank">
                Demo
              </a>
            </button>
          )}
        </div>
      </figcaption>
    </div>
  );
}

export default ProjectItems;
