import React, { useState } from "react";
import TimelineItem from "@material-ui/lab/TimelineItem";
import TimelineSeparator from "@material-ui/lab/TimelineSeparator";
import TimelineConnector from "@material-ui/lab/TimelineConnector";
import TimelineContent from "@material-ui/lab/TimelineContent";
import TimelineDot from "@material-ui/lab/TimelineDot";
import "./styles.scss";

function MyTimelineItem({ timeline }) {
  const { title, img, company, duration, expandContent, side } = timeline;
  const [open, setOpen] = useState(false);

  return (
    <TimelineItem>
      <TimelineSeparator>
        <TimelineDot />
        <TimelineConnector />
      </TimelineSeparator>
      <TimelineContent>
        <div className={`timeline-item__container ${side}`}>
          <div className="timeline-item__img">
            <img src={img} alt="" />
          </div>
          <div className="timeline-item__text">
            <div className="timeline-item__title"> {title}</div>
            <div className="timeline-item__company"> {company}</div>
            <div className="timeline-item__duration"> {duration}</div>
            <div
              className="timeline-item__show-btn"
              onClick={() => setOpen(prev => !prev)}
            >
              {(open ? "Hide" : "Show") + " details"}
            </div>
          </div>
        </div>
        {open && expandContent}
      </TimelineContent>
    </TimelineItem>
  );
}

export default MyTimelineItem;
